export default {
  name: "share",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    },
    nav: {
      type: String,
      default: ""
    },
	shopId: {
      type: String,
      default: ""
    },
	goodsId: {
	  type: String,
	  default: ""
	},
	name: {
	  type: String,
	  default: ""
	},
	img: {
	  type: String,
	  default: ""
	}
  },
  data() {
    return {
      nums: 0,
      localUrl: ''
    };
  },
  methods: {
	share(index){
		let scene = 'WXSenceTimeline'; // 朋友圈
		let title = "沁绿农业" + this.name
		if(index == 0) { // 微信
			scene = 'WXSceneSession'
			title = "沁绿农业"
		}
		let href = 'http://m.qinlvny.com/#/gooddetail/'+ this.shopId + '/' + this.goodsId
		console.log(href)
		
		let summary = this.name
		let imageUrl = this.img
		uni.share({
		    provider: "weixin",
		    scene,
		    type: 0,
		    href,
		    title,
		    summary,
		    imageUrl,
		    success: function (res) {
		        console.log("success:" + JSON.stringify(res));
		    },
		    fail: function (err) {
		        console.log("fail:" + JSON.stringify(err));
		    }
		});
		
	},
    close() {
      this.$emit("close", false);
    },
    minNums() {},
    plusNums() {},
    navigate() {
     
    }
  }
};
